import React, { Component } from 'react';
import '../../../css/App.css';

class AproposContent extends Component {
    render () {
        return (
            <div className="content">
                <h1 className="text-center">{this.props.title}</h1>
                <p>Ce site ainsi que son dveloppeur ne sont pas affilis  la RATP.</p>
                <p>Pour signaler un ou plusieurs bugs rencontrs, n'hsitez pas  contacter le dveloppeur du site  l'adresse suivante : <a href="mailto:marc_charpentier@hotmail.fr">marc_charpentier@hotmail.fr</a></p>
            </div>
        )
    }
}

export default AproposContent;


